import React from 'react'
import ImageOne from "../../images/test.svg"

const About = () => {
    return (
        <section className="grid lg:grid-cols-2 grid-cols-1
        p-5 h-screen  bg-black gap-8">
            {/* left */}
            <div className="flex items-center justify-center lg:justify-end">
             <img src={ImageOne} alt="" className="sm:h-3/4 sm:w-3/4"/>
            </div>
            {/* right */}
            <div className="flex justify-center items-center text-center md:items-start md:text-left flex-col row-start-1 lg:row-start-auto gap-1 p-8">
                <span className=" underlinetxt">Digital bank</span>
                <h2>Unlimited Transactions with zero fees</h2>
                <p>Get acces to our exclusive app that allows you to send unlimited transactions without any fees</p>
                <div className="flex sm:flex-row mt-4">
                    <button className="btn1">Get Started</button>
                    
                </div>
            </div>
        </section>
    )
}

export default About
